body{
    width:100%;
    background: rgb(158, 30, 94);
    overflow-x: hidden
    ;
}
.home{
    display:grid;
    justify-content: center;
    justify-items: center;
}
.home h2{
    color: bisque;
}
#first{
    padding-left: 1rem;
    height:1.7rem;
    outline: none;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
   

}
#second{
    height:2rem;
    background: rgb(148, 139, 145);
    border-bottom-right-radius: 5px;
    border-top-right-radius: 5px;
}
#thirst{
    margin-top: 1rem;
    height:2rem;
    background: rgb(148, 139, 145);
    border-radius: 5px;
}
.foods{
    
    
    display: flex;
    flex-wrap: wrap;
    justify-content:space-around;
}
.food{
    margin: 1rem;
    text-align: center;
    height:550px;
    width:250px;
    display:grid;
    justify-content: center;
    background: wheat;
    
    border-radius: 8px;
}
.food img{
    margin-left: 1.2rem;
    height: 200px;
    width: 200px;
    border-radius: 8px;
  
   
}
.food p{
  margin-top:-50px;
   padding: 0.2rem;
  display:-webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp:1;
  overflow: hidden;
  
}